.content-wrapper {
    display: grid;
    grid-template-columns: 6fr 6fr;
    grid-column-gap: 20px;
    grid-row-gap: 20px;
    justify-items: stretch;
    align-items: stretch;
}

/* ====navbar on landingpage==== */
.NavbarOne {
    display: flex;
    gap: 30px;
    margin-left: 10px;
}

.pdficon {
    height: 25px;
    margin-top: 38px;
    margin-left: -20px;
}

/* =======Linkedin/Github======= */
.NavbarTwo {
    display: flex;
    gap: 20px;
    justify-content: flex-end;
}

/* =====Text on landingpage===== */
.title {
    text-align: right;
    font-size: 32px;
}

.graytext {
    color: #A59C9C;
}

.colortext {
    color: #FF006B;
}

